export default {
	data() {
		return {
			PageCur: 'Home',
			TabCur: 0,
			scrollLeft: 0,
			modalNameS1: null,
			modalNameS2: null,
			modalNameS3: null,
			modalNameS4: null,
			List: [{
				name: '全部'
			}, 
			{
				name: '最新'
			}, 
			{
				name: '热门'
			},
			{
				name: '精华'
			}]
		}
	},
	methods: {
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		showModalS1(e) {
			this.modalNameS1 = e.currentTarget.dataset.target
		},
		hideModalS1(e) {
			this.modalNameS1 = null
		},
		showModalS2(e) {
			this.modalNameS2 = e.currentTarget.dataset.target
		},
		hideModalS2(e) {
			this.modalNameS2 = null
		},
		showModalS3(e) {
			this.modalNameS3 = e.currentTarget.dataset.target
		},
		hideModalS3(e) {
			this.modalNameS3 = null
		},
		showModalS4(e) {
			this.modalNameS4 = e.currentTarget.dataset.target
		},
		hideModalS4(e) {
			this.modalNameS4 = null
		},
		goMore(url) {
			uni.navigateTo({
				url: url
			});
		},
	}
}
